import {Link} from "gatsby-plugin-modal-routing";
import React from "react";

const CourseNavigation = ({ lessons, currentLesson }) => {

  function str_pad_left(string, pad, length) {
    return (new Array(length+1).join(pad)+string).slice(-length);
  }

  return (
    <div className="mt-2 p-2 lessons-container">
      <h4>Lessons</h4>
      <div className="lessons-container__list">
        <ul>
          {lessons.map(function (lessonItem) {
            const lessonTimeSeconds = lessonItem.frontmatter.time
            const minutes = Math.floor(lessonTimeSeconds / 60)
            const seconds = lessonTimeSeconds - minutes * 60
            const lessonTime = str_pad_left(minutes, '0', 2) + 'm ' + ((seconds > 0) ? seconds + 's' : '')
            const isCurrentLesson = lessonItem.frontmatter.path !== currentLesson.frontmatter.path

            return (
              <li key={lessonItem.id} className={isCurrentLesson ? 'lesson-item p-1' : 'lesson-item p-1'}>
                {isCurrentLesson ?
                  <div className="row lesson-name">
                    <div className="col-md-10">
                      <Link to={lessonItem.frontmatter.path}>
                        {lessonItem.frontmatter.title}
                      </Link>
                      <div className="timing">{lessonTime}</div>
                    </div>
                  </div>
                  :
                  <div className="row lesson-name">
                    <div className="col-md-10">
                      <div className=""> {lessonItem.frontmatter.title}</div>
                      <div className="timing">{lessonTime}</div>
                    </div>
                  </div>
                }
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  )
}

export default CourseNavigation
